import './App.css';
import Products from './Products/Products';
import  './App.css';
import {Component} from 'react';
import  bag  from "./assets/shouldert-jute-bag-500x500.jpeg";
import watch  from "./assets/OIP.jpg";
import shoe from "./assets/pexels-photo-280250.jpeg";
// import {DropdownButton} from 'react-bootstrap'



class App extends Component {
  state = {
    currency : ['INR' , 'USD'] , 
    rates : [] ,
      products :  [
               {
                   title : 'watch',
                   img : watch,
                   price : 9000
               },
               {
                   title : 'shoe',
                   img : shoe,
                   price : 5000
               },
               {
                   title : 'bag',
                   img : bag,
                   price : 10000
               }
           ]
       
  }
  componentDidMount(){
  fetch('https://api.exchangeratesapi.io/latest?base=INR')
  .then(response => response.json())
  .then(data => this.state.rates.push(data.rates['USD']));
  }

   changePrice = (id) => {
     var rates = this.state.rates;
     if (this.state.currency[id] === 'INR'){
       console.log(1)
       for(let i of this.state.products){
        console.log(i);
        this.setState({price : i.price});
      
       }
  
      
     }else if (this.state.currency[id] === 'USD'){
      
      let arr = rates.filter((rate , index) => rate['USD']);
      for(let i of this.state.products){
        console.log(i);
        this.setState({price : i.price*arr[0]})
      }
    
    }
   }
  render() {
    const currency = this.state.currency;
    return (
      <div className="App">
        <div className="dropdown">
    <button className="dropbtn">Currency</button>
      <div className="dropdown-content">
      <a href='#' onClick={() => this.changePrice(0)}>{currency[0]}</a>
      <a href='#' onClick={() => this.changePrice(1)}>{currency[1]}</a>
    </div>
    </div>
    <Products items={this.state}/>
      
      </div>
    );
  
  

    }
  }
export default App;
